<template>
    <loading v-bind:style="{opacity: is_loading, 'pointer-events': 'none'}">
        <div>
            <div class="lds-ellipsis">
                <div></div>
                <div></div>
                <div></div>
                <div></div>
            </div>
            <p>Preparing CTGUACMOJ</p>
        </div>
    </loading>
</template>

<script>
    import '@/assets/js/include.js'

    export default {
        name: "loadings",
        data() {
            return {
                is_loading: 1
            }
        },
        methods: {
            loading_hide: function () {
                if (this.is_loading > 0) {
                    this.is_loading -= 0.02;
                    var _this = this;
                    setTimeout(function () {
                        _this.loading_hide();
                    },1)
                }
            },
            loading_show: function () {
                if (this.is_loading < 1) {
                    this.is_loading += 0.1;
                    var _this = this;
                    setTimeout(function () {
                        _this.loading_show();
                    },1)
                }
            }
        },
        mounted() {
            // this.is_loading = 0;
            this.loading_hide();
        }
    }
</script>

<style lang="scss" scoped>

    loading {
        display: flex;
        z-index: 9999;
        overflow: hidden;
        position: fixed;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        justify-content: center;
        align-items: center;
        background: #f5f5f5;
        transition: .2s ease-out .0s;
        opacity: 1;

        @keyframes lds-ellipsis1 {
            0% {
                transform: scale(0);
            }
            100% {
                transform: scale(1);
            }
        }

        @keyframes lds-ellipsis3 {
            0% {
                transform: scale(1);
            }
            100% {
                transform: scale(0);
            }
        }

        @keyframes lds-ellipsis2 {
            0% {
                transform: translate(0, 0);
            }
            100% {
                transform: translate(19px, 0);
            }
        }

        .lds-ellipsis {
            display: inline-block;
            position: relative;
            width: 64px;
            height: 64px;
        }

        .lds-ellipsis div:nth-child(1) {
            left: 6px;
            animation: lds-ellipsis1 0.6s infinite;
        }

        .lds-ellipsis div:nth-child(2) {
            left: 6px;
            animation: lds-ellipsis2 0.6s infinite;
        }

        .lds-ellipsis div:nth-child(3) {
            left: 26px;
            animation: lds-ellipsis2 0.6s infinite;
        }

        .lds-ellipsis div:nth-child(4) {
            left: 45px;
            animation: lds-ellipsis3 0.6s infinite;
        }

        .lds-ellipsis div {
            position: absolute;
            top: 27px;
            width: 11px;
            height: 11px;
            border-radius: 50%;
            background: rgba(0, 0, 0, .54);
            animation-timing-function: cubic-bezier(0, 1, 1, 0);
        }

        p {
            font-weight: 300;
        }
    }

    loading > div {
        text-align: center;
    }

</style>