Vue.use(VueResource)
Vue.prototype.GLOBAL = global_
/* eslint no-unused-vars: "off"*/

/* eslint no-sparse-arrays: "off"*/
export function delCookie(cname) {
    var d = new Date();
    d.setTime(d.getTime() - (7 * 24 * 60 * 60 * 1000));
    var expires = "expires=" + d.toGMTString();
    document.cookie = cname + "=" + "; " + expires;
}

export function checkSQL(str) {
    var SQLs = "javascript,vbscript,expression,applet,meta,xml,blink,link,script,embed,object,frameset,ilayer,bgsound,base";
    SQLs += "," + "onabort,onactivate,onafterprint,onafterupdate,onbeforeactivate," +
        "onbeforecopy,onbeforecut,onbeforedeactivate,onbeforeeditfocus,onbeforepaste," +
        "onbeforeprint,onbeforeunload,onbeforeupdate,onblur,onbounce,oncellchange," +
        "onchange,onclick,oncontextmenu,oncontrolselect,oncopy,oncut,ondataavailable," +
        "ondatasetchanged,ondatasetcomplete,ondblclick,ondeactivate,ondrag,ondragend," +
        "ondragenter,ondragleave,ondragover,ondragstart,ondrop,onerror,onerrorupdate," +
        "onfilterchange,onfinish,onfocus,onfocusin,onfocusout,onhelp,onkeydown,onkeypress," +
        "onkeyup,onlayoutcomplete,onload,onlosecapture,onmousedown,onmouseenter,onmouseleave," +
        "onmousemove,onmouseout,onmouseover,onmouseup,onmousewheel,onmove,onmoveend," +
        "onmovestart,onpaste,onpropertychange,onreadystatechange,onreset,onresize," +
        "onresizeend,onresizestart,onrowenter,onrowexit,onrowsdelete,onrowsinserted," +
        "onscroll,onselect,onselectionchange,onselectstart,onstart,onstop,onsubmit,onunload";
    SQLs = SQLs.split(',');
    // console.log(SQLs);
    str = str.replace(/[\W]/g, '');
    str = str.toLowerCase();
    for (var text in SQLs) {
        text = SQLs[text];
        if (str.indexOf(text) >= 0) {
            return text;
        }
    }
    return "";
}

export function format_time(time) {
    var date = new Date(time);
    return date.toJSON().substr(0, 19).replace('T', ' ');
}

export function diff_time(time) {
    // document.cookie = cname + "=" + cvalue;

    var now = (new Date()).getTime() + 3600 * 8 * 1000;
    // console.log(now);
    var runTime = parseInt((now - time) / 1000);
    // console.log(time);
    var year = Math.floor(runTime / 86400 / 365);
    runTime = runTime % (86400 * 365);
    var month = Math.floor(runTime / 86400 / 30);
    runTime = runTime % (86400 * 30);
    var week = Math.floor(runTime / 86400 / 7);
    runTime = runTime % (86400 * 7);
    var day = Math.floor(runTime / 86400);
    runTime = runTime % 86400;
    var hour = Math.floor(runTime / 3600);
    runTime = runTime % 3600;
    var minute = Math.floor(runTime / 60);
    runTime = runTime % 60;
    var second = runTime;
    runTime = {
        year, month, week, day, hour, minute, second
    }

    return runTime;
}

export function getUrlKey(name) {
    return decodeURIComponent((new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(location.href) || [, ""])[1].replace(/\+/g, '%20')) || null
}

